<template>
  <v-bottom-navigation grow>
    <v-btn value="agenda" @click="push('/inicio')">
      <v-icon icon="mdi-home"></v-icon>
      <span>Inicio</span>
    </v-btn>
    <v-btn value="entrenamiento" @click="push('/entrenamientos')" v-if="condicion">
      <v-icon icon="mdi-weight-lifter"></v-icon>
      <span>Entrenamiento</span>
    </v-btn>
    <v-btn value="rutina" @click="push(`/entrenamientos/${rutina}`)" v-if="!condicion">
      <v-icon icon="mdi-weight-lifter"></v-icon>
      <span>Rutina</span>
    </v-btn>
    <v-btn value="clientes" @click="push('/clientes')" v-if="condicion">
      <v-icon icon="mdi-account-group">mdi-account-group</v-icon>
      <span>Clientes</span>
    </v-btn>
    <v-btn value="estadisticas" v-if="!condicion">
      <v-icon icon="mdi-account-group">mdi-file-chart</v-icon>
      <span>Estadisticas</span>
    </v-btn>
  </v-bottom-navigation>
</template>

<script>
import utiles from '../utiles/funciones'
export default {
    methods:{
      push(ruta){
        return (
          this.$store.getters.tipoUsuario==1 
          ? 
          this.$router.push(`/entrenador${ruta}`) //caso entrenador
          :
          this.$router.push(`/cliente${ruta}`) //caso cliente
        )
      }
    },
    computed:{
      condicion(){
        return (this.$store.getters.tipoUsuario == 1 ? true : false)
      },
      rutina(){
        return utiles.rutinaCliente()
      }
    }
}
</script>


