<template>
    <v-toolbar class="mb-6">
        <v-toolbar-title> Bienvenido, {{ username }}</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn  @click="push()">
            <v-icon icon="mdi-account"></v-icon>
            <span>Perfil</span>
        </v-btn>
    </v-toolbar>
</template>

<script>
    export default {
        data(){
            return{
            }
        },
        methods:{
            push(){
                return (
                    this.$store.getters.tipoUsuario == 1
                    ?
                    this.$router.push(`/entrenador/perfil`)
                    :
                    this.$router.push(`/cliente/perfil`)
                )
            }
        },
        computed:{
            username(){
                return this.$store.getters.profileInfo.nombre
            }
        }
    }
</script>